<template>
  <v-card-text>
    <lora-generic-bacnet-point-mapping-form
      :serviceMappings="serviceMappings"
      :parentPoints="loraPoints"
      :child1Points="bacnetPoints"
      :child2Points="genericPoints"
      :lora-devices="loraDevices"
      :genericNetworkDevices="genericNetworkDevices"
      :genericNetworks="genericNetworks"
      parentMappingKey="loraPoint"
      child1MappingKey="bacnetPoint"
      child2MappingKey="genericPoint"
      :createServiceMapping="createServiceMapping"
      :createLoraBacnetGenericPointMapping="createLoraBacnetGenericPointMapping"
      parentFieldTitle="LoRa Points"
      child1FieldTitle="BACnet Points"
      child2FieldTitle="Rubix Points"
    >
    </lora-generic-bacnet-point-mapping-form>
    <v-divider></v-divider>
    <lora-generic-bacnet-point-mapping-grid
      :loading="isFetchingGridData"
      :sync-grid-items="syncData"
      file-name="lora-bacnet-generic-service-mapping"
      :format-data-service="formatExportData"
      :parentPoints="loraPoints"
      :child1Points="bacnetPoints"
      :child2Points="genericPoints"
      parentMappingKey="loraPoint"
      child1MappingKey="bacnetPoint"
      :fix-mapping="fixMapping"
      child2MappingKey="genericPoint"
      :serviceMappings="serviceMappings"
      :deleteItem="deleteServiceMapping"
      :deleteItems="deleteServiceMappings"
      parentHeaderTitle="LoRa Points"
      child1HeaderTitle="BACnet Points"
      child2HeaderTitle="Rubix Points"
      :receive-data-rows="receiveDataRows"
      :updateServiceMapping="updateServiceMapping"
    ></lora-generic-bacnet-point-mapping-grid>

    <lora-generic-bacnet-mapping-import-dialog
      v-if="openImportMappingDialog"
      :lora-points="loraPoints"
      :bacnet-points="bacnetPoints"
      :generic-points="genericPoints"
      :close="closeImportMappingDialog"
      :is-open="openImportMappingDialog"
      :data-rows="dataRows"
      :sync-data="syncData"
    >
    </lora-generic-bacnet-mapping-import-dialog>
  </v-card-text>
</template>
<script>
import LoraGenericBacnetPointMappingForm from './LoraGenericBacnetPointMappingForm';
import LoraGenericBacnetPointMappingGrid from './LoraGenericBacnetPointMappingGrid';
import * as pointServerService from '../../services/pointServerService';
import * as loraBacnetGenericPointsService from '../../services/loraBacnetGenericPointsService';
import LoraGenericBacnetMappingImportDialog from './LoraGenericBacnetMappingImportDialog.vue';
import {
  CREATE_MAPPING_SERVICE,
  DELETE_MAPPING_SERVICE,
  DELETE_MAPPING_SERVICES,
  UPDATE_MAPPING_SERVICE,
} from '../../constants/messageConstants';

export default {
  name: 'lora-generic-bacnet-points-mapping',
  components: {
    LoraGenericBacnetPointMappingForm,
    LoraGenericBacnetPointMappingGrid,
    LoraGenericBacnetMappingImportDialog,
  },
  data() {
    return {
      dataRows: [],
      isUpdating: false,
      serviceMappings: [],
      genericPoints: [],
      bacnetPoints: [],
      loraPoints: [],
      loraDevices: [],
      loraPointsMap: {},
      genericPointsMap: {},
      bacnetPointsMap: {},
      genericNetworks: [],
      genericNetworkDevices: [],
      isFetchingGridData: false,
      openImportMappingDialog: false,
    };
  },
  mounted() {
    this.syncData();
  },
  methods: {
    syncData() {
      this.fetchServiceMapping();

      pointServerService
        .fetchGenericDevices()
        .then(({ genericNetworks, genericDevices }) => {
          this.genericNetworks = genericNetworks;
          this.genericNetworkDevices = genericDevices;
        })
        .catch(err => {});

      pointServerService
        .fetchRubixPSGenericPoints()
        .then(data => {
          this.genericPointsMap = this.createMap(data);
          this.genericPoints = data;
        })
        .catch(err => {});

      pointServerService
        .fetchBacnetPoints()
        .then(data => {
          this.bacnetPoints = data.map(item => ({ ...item, id: item.uuid, name: item.object_name }));
          this.bacnetPointsMap = this.createMap(this.bacnetPoints);
        })
        .catch(err => {});

      pointServerService
        .fetchLoraPoints()
        .then(({ loraPoints, loraDevices }) => {
          this.loraPoints = loraPoints;
          this.loraDevices = loraDevices;
          this.loraPointsMap = this.createMap(this.loraPoints, 'name');
        })
        .catch(err => {});
    },
    fetchServiceMapping() {
      this.isFetchingGridData = true;
      return loraBacnetGenericPointsService
        .fetch()
        .then(data => {
          this.serviceMappings = data;
        })
        .finally(() => {
          this.isFetchingGridData = false;
        });
    },
    createServiceMapping(mapping) {
      return loraBacnetGenericPointsService
        .createServiceMapping(mapping)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(CREATE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(CREATE_MAPPING_SERVICE.error, { type: 'error' });
        })
        .finally(() => {});
    },
    updateServiceMapping(mapping, uuid) {
      return loraBacnetGenericPointsService
        .updateServiceMapping(mapping, uuid)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(UPDATE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(UPDATE_MAPPING_SERVICE.error, { type: 'error' });
        });
    },
    deleteServiceMappings(serviceMappings) {
      return loraBacnetGenericPointsService
        .deleteServiceMappings(serviceMappings)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(DELETE_MAPPING_SERVICES.success);
        })
        .catch(err => {
          this.$toasted.show(DELETE_MAPPING_SERVICES.error, { type: 'error' });
        });
    },
    deleteServiceMapping(serviceMapping) {
      return loraBacnetGenericPointsService
        .deleteServiceMapping(serviceMapping)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(DELETE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(DELETE_MAPPING_SERVICE.error, { type: 'error' });
          console.error(err);
        });
    },
    formatExportData(dataItems, actionType) {
      if (actionType === 'copy') {
        return dataItems
          .map(loraBacnetGenericPointsService.mapDataToExport)
          .map(item =>
            loraBacnetGenericPointsService.appendNetworkDetails(
              item,
              this.loraPointsMap,
              this.bacnetPointsMap,
              this.genericPointsMap,
            ),
          );
      }
      const data = loraBacnetGenericPointsService.prepareFileExport(
        dataItems
          .map(loraBacnetGenericPointsService.mapDataToExport)
          .map(item =>
            loraBacnetGenericPointsService.appendNetworkDetails(
              item,
              this.loraPointsMap,
              this.bacnetPointsMap,
              this.genericPointsMap,
            ),
          ),
      );
      return data;
    },
    createLoraBacnetGenericPointMapping(serviceMappings, autoCreateDevice, autoCreateBacnetPoints) {
      return loraBacnetGenericPointsService
        .bulkCreateServiceMapping(serviceMappings, { autoCreateDevice, autoCreateBacnetPoints })
        .then(data => {
          this.fetchServiceMapping();
          return data;
        });
    },
    createMap(points, key = 'name') {
      return (points || []).reduce((acc, curr) => {
        acc[curr[key]] = curr;
        return acc;
      }, {});
    },
    receiveDataRows(dataRows = []) {
      if (dataRows.length >= 1) {
        this.dataRows = loraBacnetGenericPointsService.prepareFileImport(dataRows);
        this.openImportMappingDialog = true;
      }
    },
    closeImportMappingDialog() {
      this.openImportMappingDialog = false;
    },
    fixMapping() {
      return loraBacnetGenericPointsService.fixMapping().then(() => {
        this.fetchServiceMapping();
      });
    },
  },
};
</script>
