<template>
  <div>
    <grid-toolbar
      toolbarTitle="Mappings"
      :items="items"
      :hide-export-data="true"
      :is-delete-enabled="isDeleteEnabled"
      :on-delete="() => openDialog(dialogNames.deleteServiceMapping, 'multiple')"
      :on-copy="onCopy"
      :file-name="fileName"
      :format-data-service="formatDataService"
      :receive-data-rows="receiveDataRows"
    >
      <template v-slot:search>
        <v-spacer/><v-spacer/><v-spacer/><v-spacer/>
        <v-text-field
          v-model="search"
          append-icon="fa-search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </template>
      <template v-slot:customAction>
        <v-btn @click="fixServiceMappings" x-small color="blue" class="mr-2 btn-height-22 white--text">
          Sync
          <v-progress-circular
            class="ml-3"
            v-if="isFixingServiceMaps"
            width="2"
            indeterminate
            size="12"
          ></v-progress-circular>
          <v-icon v-else x-small right> fa-refresh </v-icon>
        </v-btn>
        <tooltip-icon-button
          btnColor="default"
          btnIcon="fa-refresh"
          btnTooltip="Refresh mapping table"
          buttonSize="small"
          :onClick="() => syncGridItems()"
        ></tooltip-icon-button>
      </template>
    </grid-toolbar>
    <v-data-table
      show-select
      item-key="uuid"
      v-model="selectedItems"
      :loading="loading"
      :headers="headers"
      :search="search"
      :items="items"
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
    >
      <template v-slot:[`item.mapping_state`]="{ item }">
        <app-status-chip :state="item.mapping_state"></app-status-chip>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-btn @click="() => toggleServiceMappingDialog(item)" icon small color="primary">
          <v-icon small dark> fa-pencil </v-icon>
        </v-btn>
        <v-btn
          @click="() => openDialog(dialogNames.deleteServiceMapping, 'single', item)"
          icon
          small
          color="error"
        >
          <v-icon small dark> fa-trash-o </v-icon>
        </v-btn>
      </template>
    </v-data-table>
    <confirm-dialog
      :is-open="dialog[dialogNames.deleteServiceMapping]"
      :loading="isDeletingItem"
      :on-close="() => closeDialog(dialogNames.deleteServiceMapping)"
      :on-confirm="() => handleDeleteItem()"
      dialog-text="Do you want to delete the mapping(s)?"
    ></confirm-dialog>
    <v-dialog v-model="editServiceMappingDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit Mapping</span>
        </v-card-title>
        <v-card-text>
          <service-mapping-form
            :service-mappings="serviceMappings"
            :mapping-list-right="mappingListRight"
            :mapping-list-left="mappingListLeft"
            :mapping-selection-right-title="mappingRightTitle"
            :mapping-selection-left-title="mappingLeftTitle"
            :create-service-mapping="updateServiceMapping"
            :mapping-left-key="mappingLeftKey"
            :mapping-right-key="mappingRightKey"
            :is-edit-form="true"
            :on-cancel="() => toggleServiceMappingDialog()"
            :service-mapping="selectedItem"
          ></service-mapping-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import AppStatusChip from '../Services/AppStatusChip.vue';
import ConfirmDialog from '../Common/ConfirmDialog';
import GridToolbar from '../Common/GridToolbar';
import ServiceMappingForm from './ServiceMappingForm';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import { DATA_TABLE_PROPS, MAPPING_STATE } from '../../constants/appConstant';

export default {
  name: 'service-mapping-grid',
  props: [
    'loading',
    'mappingRightKey',
    'mappingListRight',
    'mappingListLeft',
    'mappingLeftKey',
    'serviceMappings',
    'deleteItem',
    'editItem',
    'fileName',
    'formatDataService',
    'mappingRightTitle',
    'mappingLeftTitle',
    'updateServiceMapping',
    'receiveDataRows',
    'deleteItems',
    'fixMapping',
    'syncGridItems'
  ],
  components: { ConfirmDialog, ServiceMappingForm, GridToolbar, AppStatusChip, TooltipIconButton },
  data() {
    const dialogNames = {
      deleteServiceMapping: 'deleteServiceMapping',
    };
    return {
      search: '',
      isFixingServiceMaps: false,
      deletionType: null,
      selectedItems: [],
      isDeletingItem: false,
      selectedItem: null,
      editServiceMappingDialog: false,
      dialog: {
        [dialogNames.deleteServiceMapping]: false,
      },
      dialogNames: dialogNames,
      headers: [
        {
          text: this.mappingLeftTitle,
          align: 'start',
          value: 'serviceLeft',
        },
        {
          text: this.mappingRightTitle,
          align: 'start',
          value: 'serviceRight',
        },
        {
          text: 'Mapping status',
          align: 'center',
          value: 'mapping_state',
        },
        {
          text: 'Actions',
          align: 'start',
          value: 'actions',
          sortable: false,
        },
      ],
      tableProps: { ...DATA_TABLE_PROPS },
    };
  },
  computed: {
    items() {
      return this.serviceMappings.map(val => ({
        ...val,
        isMapped: val.mapping_state === MAPPING_STATE.MAPPED,
        mapping_state: val.mapping_state || '-',
        serviceLeft: (val[this.mappingLeftKey] && val[this.mappingLeftKey].name) || '-',
        serviceRight: (val[this.mappingRightKey] && val[this.mappingRightKey].name) || '-',
      }));
    },
    isDeleteEnabled() {
      return this.selectedItems.length > 0;
    },
  },
  methods: {
    openDialog(dialogName, deletionType, item) {
      this.dialog[dialogName] = true;
      this.deletionType = deletionType;
      if (deletionType == 'single') {
        this.selectedItem = item;
      }
    },
    toggleServiceMappingDialog(item) {
      this.editServiceMappingDialog = !this.editServiceMappingDialog;
      if (this.editServiceMappingDialog) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = null;
      this.deletionType = null;
    },
    handleDeleteItem() {
      switch (this.deletionType) {
        case 'single':
          this.onDeleteRow();
          break;
        case 'multiple':
          this.onDeleteRows();
          break;
      }
    },
    onCopy(copyToClipboard) {
      const formattedData = this.formatDataService(
        this.selectedItems.filter(val => val.isMapped),
        'copy',
      );
      copyToClipboard(JSON.stringify(formattedData));
    },
    onDeleteRow() {
      this.isDeletingItem = true;
      this.deleteItem(this.selectedItem)
        .then(() => {
          this.closeDialog(this.dialogNames.deleteServiceMapping);
        })
        .finally(() => {
          this.isDeletingItem = false;
        });
    },
    onDeleteRows() {
      if (this.selectedItems.length > 0) {
        this.isDeletingItem = true;
        this.deleteItems(this.selectedItems)
          .then(() => {
            this.closeDialog(this.dialogNames.deleteServiceMapping);
          })
          .finally(() => {
            this.isDeletingItem = false;
          });
      }
    },
    fixServiceMappings() {
      if (typeof this.fixMapping === 'function') {
        this.isFixingServiceMaps = true;
        this.fixMapping().finally(() => {
          this.isFixingServiceMaps = false;
        });
      }
    },
  },
};
</script>
