<template>
  <div>
    <grid-toolbar
      :on-copy="onCopy"
      :items="items"
      toolbarTitle="Mappings"
      :file-name="fileName"
      :format-data-service="formatDataService"
      :receive-data-rows="receiveDataRows"
      :is-delete-enabled="isDeleteEnabled"
      :on-delete="() => toggleDeleteDialog(null, 'multiple')"
    >
      <template v-slot:search>
        <v-text-field
          v-model="search"
          append-icon="fa-search"
          label="Search"
          single-line
          hide-details
          class="pl-10"
        ></v-text-field>
      </template>
      <template v-slot:customAction>
        <v-btn x-small color="blue" class="mr-1 btn-height-22 white--text" @click="fixServiceMappings">
          Sync
          <v-progress-circular
            class="ml-3"
            v-if="isFixingServiceMaps"
            width="2"
            indeterminate
            size="12"
          ></v-progress-circular>
          <v-icon v-else x-small right dark> fa-refresh </v-icon>
        </v-btn>
        <tooltip-icon-button
          btnColor="default"
          btnIcon="fa-refresh"
          btnTooltip="Refresh mapping table"
          buttonSize="small"
          :onClick="() => syncGridItems()"
        ></tooltip-icon-button>
      </template>
    </grid-toolbar>
    <v-data-table
      item-key="uuid"
      v-model="selectedItems"
      show-select
      :loading="loading"
      :search="search"
      :headers="headers"
      :items="items"
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
    >
      <template v-slot:[`item.actions`]="{ item }">
        <v-btn @click="() => toggleServiceMappingDialog(item)" icon small color="primary">
          <v-icon small dark> fa-pencil </v-icon>
        </v-btn>
        <v-btn @click="() => toggleDeleteDialog(item, 'single')" icon small color="error">
          <v-icon small dark> fa-trash-o </v-icon>
        </v-btn>
      </template>
      <template v-slot:[`item.mapping_state`]="{ item }">
        <app-status-chip :state="item.mapping_state"></app-status-chip>
      </template>
    </v-data-table>
    <confirm-dialog
      :is-open="deleteServiceMappingDialog"
      :loading="isDeletingItem"
      :on-close="() => closeDeleteDialog()"
      :on-confirm="() => handleDeleteItem()"
      dialog-text="Do you want to delete the mapping?"
    ></confirm-dialog>
    <v-dialog v-model="editServiceMappingDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit Mapping</span>
        </v-card-title>
        <v-card-text>
          <parent-child-mapping-form
            v-if="editServiceMappingDialog"
            :is-edit-form="true"
            :on-cancel="() => toggleServiceMappingDialog()"
            :service-mapping="selectedItem"
            :parentPoints="parentPoints"
            :child1Points="child1Points"
            :child2Points="child2Points"
            :parentMappingKey="parentMappingKey"
            :child1MappingKey="child1MappingKey"
            :child2MappingKey="child2MappingKey"
            :parentFieldTitle="parentHeaderTitle"
            :child1FieldTitle="child1HeaderTitle"
            :child2FieldTitle="child2HeaderTitle"
            :serviceMappings="serviceMappings"
            :createServiceMapping="updateServiceMapping"
          >
          </parent-child-mapping-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import AppStatusChip from '../Services/AppStatusChip.vue';
import GridToolbar from '../Common/GridToolbar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ParentChildMappingForm from './ParentChildMappingForm';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import { DATA_TABLE_PROPS, MAPPING_STATE } from '../../constants/appConstant';

export default {
  name: 'service-mapping-grid',
  components: { ConfirmDialog, ParentChildMappingForm, GridToolbar, AppStatusChip, TooltipIconButton },
  props: [
    'loading',
    'parentMappingKey',
    'child1MappingKey',
    'child2MappingKey',
    'serviceMappings',
    'parentPoints',
    'child1Points',
    'child2Points',
    'updateServiceMapping',
    'deleteItem',
    'deleteItems',
    'parentHeaderTitle',
    'child1HeaderTitle',
    'child2HeaderTitle',
    'fileName',
    'receiveDataRows',
    'formatDataService',
    'fixMapping',
    'syncGridItems',
  ],

  data() {
    return {
      search: '',
      isFixingServiceMaps: false,
      deleteServiceMappingDialog: false,
      isDeletingItem: false,
      selectedItem: null,
      selectedItems: [],
      deletionType: null,
      editServiceMappingDialog: false,
      headers: [
        {
          text: this.parentHeaderTitle,
          align: 'start',
          value: 'parentName',
        },
        {
          text: this.child1HeaderTitle,
          align: 'start',
          value: 'child1Name',
        },
        {
          text: this.child2HeaderTitle,
          align: 'start',
          value: 'child2Name',
        },
        {
          text: 'Mapping State',
          align: 'center',
          value: 'mapping_state',
        },
        {
          text: 'Actions',
          align: 'start',
          value: 'actions',
          sortable: false,
        },
      ],
      tableProps: { ...DATA_TABLE_PROPS },
    };
  },
  computed: {
    items() {
      return this.serviceMappings.map(val => ({
        ...val,
        isMapped: val.mapping_state === MAPPING_STATE.MAPPED,
        parentName: val[this.parentMappingKey].name,
        child1Name: (val[this.child1MappingKey] && val[this.child1MappingKey].name) || '-',
        child2Name: (val[this.child2MappingKey] && val[this.child2MappingKey].name) || '-',
      }));
    },
    isDeleteEnabled() {
      return this.selectedItems.length > 0;
    },
  },
  methods: {
    toggleDeleteDialog(item, deletionType = null) {
      this.deletionType = deletionType;
      this.deleteServiceMappingDialog = !this.deleteServiceMappingDialog;
      if (item && this.deleteServiceMappingDialog) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    toggleServiceMappingDialog(item) {
      this.editServiceMappingDialog = !this.editServiceMappingDialog;
      if (this.editServiceMappingDialog) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDeleteDialog() {
      this.deleteServiceMappingDialog = false;
      this.selectedItem = null;
      this.deletionType = null;
    },
    handleDeleteItem() {
      switch (this.deletionType) {
        case 'single':
          this.onDeleteRow();
          break;
        case 'multiple':
          this.onDeleteRows();
          break;
      }
    },
    onDeleteRows() {
      if (this.selectedItems.length > 0) {
        this.isDeletingItem = true;
        this.deleteItems(this.selectedItems)
          .then(() => {
            this.closeDeleteDialog();
          })
          .finally(() => {
            this.isDeletingItem = false;
          });
      }
    },
    onDeleteRow() {
      this.isDeletingItem = true;
      this.deleteItem(this.selectedItem)
        .then(() => {
          this.closeDeleteDialog();
        })
        .finally(() => {
          this.isDeletingItem = false;
        });
    },
    onCopy(copyToClipboard) {
      const formattedData = this.formatDataService(this.selectedItems.filter(val => val.isMapped));
      copyToClipboard(JSON.stringify(formattedData));
    },
    fixServiceMappings() {
      if (typeof this.fixMapping === 'function') {
        this.isFixingServiceMaps = true;
        this.fixMapping().finally(() => {
          this.isFixingServiceMaps = false;
        });
      }
    },
  },
};
</script>
